.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;

  &.--frozen {
      overflow: hidden;
  }
}

.n-container {
  width: 100%;
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

.text {
  p{
    font-size: 20px;
    line-height: 140%;
    & + p{
      margin-top: 28px;
    }
  }
}


a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}



h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
  letter-spacing: -0.02em;
  margin: 0;
}

.n-title{
  margin-bottom: 24px;
}

h1{
  font-weight: 600;
  font-size: 48px;
  line-height: 1.1;
}

h2{
  font-weight: 600;
  font-size: 48px;
  line-height: 1.1;
}

h3{
  font-weight: 600;
  font-size: 36px;
  line-height: 49px;
}
h4{
  font-weight: 600;
  font-size: 24px;
  line-height: 110%;
}
