/* Main Colors */
$color-base : #0F2D79;
$color-bg : #fff;
$color-text : $color-base;
$color-overlay : $color-base;
$opacity-overlay: .4;
$link-color : $color-base;

$orange: #FF6433;
$blue: #265DEB;
$green: #AAEB46;





/* Fonts */
$font-family : 'Nunito Sans', sans-serif;
$font-title: $font-family;
$font-size : 16px;
$font-weight : normal;
$letter-spacing : normal;
$line-height : 1.2;
$placeholder: rgba(#0F2D79, .5);



/*container*/
$container-padding-left: 24px;
$container-padding-right: 24px;
$container-width: 1670px + $container-padding-left + $container-padding-right;

