.n-header{
  background-color: #fff;
  width: 100%;
  &__top{
    padding-top: 30px;
    padding-bottom: 30px;
  }
  &__left{
    width: 100%;
    max-width: 270px;
  }
  &__center{
    width: calc(100% - 470px);
    padding-left: 25px;
  }
  &__right{
    max-width: 200px;
    width: 100%;
  }
  &__pays {
    width: 200px;
    display: flex;
    align-items: center;
  }
  &__basket{
    width: 63px;
    min-height: 34px;
    padding: 6px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $orange;
    border-radius: 4px;
    margin-right: 17px;
    svg{
      fill: #fff;
    }
  }
  &__basket-number{
    font-weight: 800;
    font-size: 16px;
    line-height: 16px;
    color: #fff;
    padding-right: 6px;
    border-right: 1px solid #fff;
    margin-right: 6px;
  }
  &__basket-ico{
    fill: #fff;
  }
  &__price{
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
  }
  &__nav {
    background-color: #0F2D79;
    display: flex;
    align-items: center;
  }
}

.n-search {
  position: relative;
  width: 357px;
  align-self: flex-start;
  margin-right: 24px;
  &__input{
    height: 48px;
    width: 100%;
    border-radius: 4px;
    background-color: #F5F6FA;
    padding-left: 27px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    &::placeholder{
      color: #0F2D79;
    }
  }
  &__button{
    position: absolute;
    z-index: 10;
    right: 17px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.n-contact{
  margin-right: 24px;
  &__bottom {
    display: flex;
    align-items: center;
  }
  &__phone{
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
  }
  &__days{
    font-weight: bold;
    font-size: 10px;
    line-height: 14px;
    width: 39px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F5F6FA;
    border-radius: 2px;
    margin-right: 8px;
  }
  &__address{
    font-size: 12px;
    line-height: 16px;
    white-space: nowrap;
  }
}

.n-nav{
  margin-bottom: 36px;
  &__link{
    height: 56px;
    padding-left: 12px;
    padding-right: 12px;
    font-weight: 600;
    font-size: 20px;
    line-height: 1;
    color: #fff;
    display: flex;
    align-items: center;
    cursor: pointer;
    &.active:after{
      opacity: 1;
    }
    &:after{
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 4px;
      border-radius: 3px;
      background-color: $orange;
      transition: .4s;
      opacity: 0;
    }
    &--green{
      color: $green;
    }
    &:not(:last-child){
      margin-right: 24px;
    }
    svg{
      margin-left: 6px;
    }
  }
}

.close{
  position: absolute;
  right: 35px;
  top: 24px;
  z-index: 250;
}

.mobile-header {
  display: none;
}

.mobile-menu{
  display: none;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 12px;
  padding: 100px 32px 18px;
  z-index: 100;
  overflow-y: auto;
  transform: translateY(100%);
  transition: .4s;
  &.active{
    transform: translateY(0);
  }
  &__top{
    display: flex;
    background-color: #fff;
    border-bottom: 2px solid #E5E9F3;
    margin-bottom: 30px;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    padding: 18px 0 0;
    z-index: 50;
    width: 100%;
    padding-left: 32px;
  }
  &__tabs-item{
    font-size: 24px;
    line-height: 33px;
    padding-bottom: 14px;
    border-bottom: 2px solid transparent;
    cursor: pointer;
    transform: translateY(1px);
    &:not(:last-child){
      margin-right: 56px;
    }
    &.active{
      border-color: $orange;
      font-weight: bold;
    }
  }
  
}

.mobile-header{
  .n-header__basket{
    color: #fff;
    svg{
      fill: #fff;
    }
  }
}

.accardeon__item{
  margin-bottom: 30px;
}

.accardeon__main{
  font-size: 24px;
  padding-right: 35px;
  position: relative;
  cursor: pointer;

 

  &.active{
    font-weight: bold;
    svg{
      transform: rotateZ(180deg);
      stroke: #0F2D79;
    }
  }
  svg{
    stroke: #FF6433; 
    stroke-width: 2px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    transition: .4s;
  }
}

.accardeon__drop{
  padding: 18px 0;
  display: none;
  .accardeon__main{
    font-size: 18px;
  }
  .accardeon__drop{
    padding: 14px 0 24px;
  }
  .accardeon__item{
    margin-bottom: 18px;
  }
}

.accardeon-menu{
  .mobile__link{
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 6px;
    display: block;
  }
}

.mobile-menu__tab{
  display: none;
}

.mobile-menu__tab.active{
  display: block;
}

.mobile__link{
  font-size: 24px;
  line-height: 100%;
  margin-bottom: 30px;
  display: block;
}

.catalog__accardeon-link{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 3px;
  padding-top: 3px;
  border-bottom: 1px solid #E5E9F3;
  
  &:hover{
    span:first-child{
      color: $orange;
    }
  }
  span{
    font-size: 16px;
    line-height: 22px;
    transition: .4s;
    &:last-child{
      font-size: 12px;
    }
  }
}

@media(max-width: 1450px){
  .n-header__pays{
    display: none;
  }
  .n-nav__link{
    font-size: 16px;
    height: 50px;
  }
  .n-nav__link:not(:last-child){
    margin-right: 0px;
  }
}

@media(max-width: 1320px){
  .n-header__left{
    max-width: 200px;
  }

  .n-header__center{
    width: calc(100% - 401px);
  }
}


@media(max-width: 1250px){
  .n-search{
    display: none;
  }
}

@media(max-width: 1100px){
  .n-nav__link{
    font-size: 14px;
  }
}

@media(max-width: 980px){
  .mobile-menu{
    display: block;
  }
  .n-header__contacts{
    display: none;
  }
  .n-nav{
    display: none;
  }
  .n-header__right{
    display: none;
  }

  .n-header__top{
    padding-top: 25px;
    padding-bottom: 0;
    justify-content: center;
  }

  .n-header__center{
    display: none;
  }

  .mobile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    left: 50%;
    bottom: 7px;
    transform: translateX(-50%);
    z-index: 200;
    width: calc(100% - 24px);
    background-color: #0F2D79;
    height: 56px;
    border-radius: 12px;
    padding-left: 28px;
    padding-right: 28px;
  }
  

  .burger{
    display: flex;
    align-items: center;
    font-weight: 800;
    font-size: 16px;
    line-height: 22px;
    color: #fff;
    &.active{
      color: #AAEB46;
      svg{
        stroke: #AAEB46;
      }
    }
    svg{
      margin-right: 12px;
      stroke: white;
      stroke-width: 2px;
      stroke-linecap: round;
    }
  }

  .mobile-header__icons{
    display: flex;
    justify-content: flex-end;
  }

  .mobile-search{
    margin-right: 30px;
  }
}