.crumbs{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &__link{
    font-size: 14px;
    line-height: 100%;
    line-height: 1.3;
    color: #8D9DC5;
    margin-right: 12px;
    padding-right: 21px;
    position: relative;
    &:not(:last-child){
      color: #0F2D79;
      &:after{
        opacity: 1;
      }
    }
    &:after{
      opacity: 0;
      content: "";
      width: 10px;
      height: 4px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      background: url(../img/crumbs.svg) no-repeat center / 10px auto;
    }
  }
}


.n-catalog{
  padding-bottom: 96px;
  &__pic{
    height: 180px;
  }
  &__scroll{
    height: auto;
    max-height: 280px;
    overflow-y: auto;
    padding: 18px 40px;
  }
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin-right: -24px;
  }
  &__item{
    background-color: #F5F6FA;
    border: 1px solid #E5E9F3;
    border-radius: 4px;
    display: block;
    margin-right: 24px;
    width: calc(20% - 24px);
    margin-bottom: 24px;
    align-self: flex-start;
  }
  &__title{
    font-weight: 800;
    font-size: 24px;
    line-height: 100%;
    margin-bottom: 24px;
  }
  &__small-title{
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
  }
  &__li{
    display: flex;
    justify-content: space-between;
    padding-top: 6px;
    padding-bottom: 4px;
    align-items: center;
    border-bottom: 1px solid #E5E9F3;
    span:last-child{
      font-size: 12px;
    }
  }

  &.--mobile{
    display: none;
  }
}

.n-popular{
  padding-bottom: 76px;
  &__list{
    display: flex;
    flex-wrap: wrap;
    margin-right: -24px;
  }
  &__item{
    border: 1px solid #E5E9F3;
    border-radius: 4px;
    background-color: #fff;
    border-radius: 4px;
    padding: 12px 36px 24px;
    position: relative;
    margin-bottom: 24px;
    width: calc(20% - 24px);
    margin-right: 24px;
  }
  &__tag{
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translateX(-50%);
    font-weight: 800;
    font-size: 10px;
    line-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    color: #265DEB;
    z-index: 20;
    
    img{
      margin-right: 4px;
    }
  }
  &__pic{
    display: flex;
    justify-content: center;
    margin-bottom: 12px;
    position: relative;
    z-index: 20;
  }
  &__title{
    font-weight: bold;
    font-size: 16px;
    line-height: 120%;
    color: #0F2D79;
    max-height: 255px;
    margin-bottom: 18px;
    position: relative;
    z-index: 20;
  }
  &__btns{
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 20;
    .--pay{
      height: 42px;
      font-size: 16px;
      line-height: 110%;
      display: none;
      &.active{
        display: flex;
      }
      sup{
        font-size: 12px;
      }
      span:first-child{
        font-weight: 900;
        padding-right: 14px;
        margin-right: 14px;
        border-right: 1px solid #fff;
      }
    }
  }
  &__select{
    margin-bottom: 8px;
  }
  &__btn-left{
    width: calc(100% - 50px);
  }
  &__btn-question{
    width: 42px;
    height: 42px;
    border-radius: 4px;
    border: 1px solid #E5E9F3;
    background-color: #F5F6FA;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    z-index: 20;
    &.active{
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }

  &__disabled {
    width: 100%;
    height: 42px;
    background-color: #E5E9F3;
    border-radius: 4px;
    font-size: 14px;
    line-height: 110%;
    color: #0F2D79;
    justify-content: center;
    align-items: center;
    display: none;
    position: relative;
    z-index: 20;
  }

  &__added{
    justify-content: center;
    height: 42px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: bold;
    font-size: 14px;
    line-height: 110%;
    color: #fff;
    background-color: #265DEB;
    align-items: center;
    display: none;
    position: relative;
    z-index: 20;
    svg{
      margin-right: 8px;
    }
    &.active{
      display: flex;
    }
    
  }

  &__count{
    display: none;
    position: relative;
    z-index: 20;
    &.active{
      display: block;
    }
  }
  &__info{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    background-color: #fff;
    box-shadow: 0px 16px 48px #E5E9F3;
    border-radius: 4px;
    z-index: 10;
    display: flex;
    flex-wrap: wrap;
    display: none;
    .n-catalog__ul{
      position: absolute;
      top: 100%;
      width: calc(100% + 2px);
      left: -1px;
      background-color: #fff;
      border: 1px solid #E5E9F3;
      border-top: 1px solid #fff;
      padding: 0 36px 36px;
      box-shadow: 0px 42px 24px #e5e9f3;
      z-index: 200;
      border-radius: 0 0 4px 4px;
    }
  }
}


.n-select{
  position: relative;
    z-index: 20;
  &.--top{
    .n-select__main.active{
      border-radius: 0 0 4px 4px;
    }
    .n-select__list{
      bottom: 100%;
      margin-bottom: -1px;
      top: auto;
    }
  }
  &__main{
    position: relative;
    cursor: pointer;
    height: 42px;
    width: 100%;
    border: 1px solid #E5E9F3;
    border-radius: 4px;
    background-color: #fff;
    display: flex;
    padding-left: 12px;
    font-weight: 600;
    padding-right: 30px;
    &.active{
      border-radius: 4px 4px 0 0;
      .n-select__arrow svg{
        transform: rotateZ(180deg);
        stroke: #0F2D79;
      }
    }
  }
  &__name{
    font-size: 14px;
    white-space: nowrap;
    width: calc(100% - 42px);
  }
  &__main-info{
    display: flex;
    align-items: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    width: 100%;
  }
  &__pic {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 12px;
  }
  &__arrow {
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 20;
    svg{
      transition: .4s;
      stroke: $orange;
      stroke-width: 2px;
    }
  }
  &__list{
    position: absolute;
    background-color: #fff;
    width: 100%;
    border: 1px solid #E5E9F3;
    border-radius: 4px 4px 0 0;
    z-index: 100;
    left: 0;
    top: 100%;
    margin-top: -1px;
    display: none;
  }
  &__item{
    display: flex;
    align-items: center;
    padding-top: 13px;
    padding-bottom: 13px;
    padding-left: 12px;
    cursor: pointer;
    transition: .4s;
    &.active{
      background-color: #F5F6FA;
    }
    &:hover{
      background-color: #F5F6FA;
    }
  }
}


.count{
  display: flex;
  justify-content: center;
  border: 1px solid #E5E9F3;
  background-color: #fff;
  border-radius: 4px;
  &__input{
    width: calc(100% - 104px);
    position: relative;
    input{
      text-align: center;
      height: 42px;
      font-size: 20px;
      line-height: 110%;
      width: 100%;
    }
  }
  &__description{
    position: absolute;
    bottom: 0;
    right: 4px;
    font-size: 14px;
    line-height: 110%;
  }
  &__minus{
    border-right: 1px solid #E5E9F3;
  }
  &__plus{
    border-left: 1px solid #E5E9F3;
  }
  &__minus,
  &__plus{
    cursor: pointer;
    width: 48px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__close{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 50;    
    cursor: pointer;
  }
}


.n-main-description{
