.checkbox{
  display: flex;
  background-color: #F5F6FA;
  border: 1px solid #E5E9F3;
  cursor: pointer;
  height: 50px;
  border-radius: 4px;
  width: calc(50% - 8px);
  font-weight: bold;
  margin-bottom: 16px;
  align-items: center;
  justify-content: center;
  transition: .4s;
  &:hover{
    border-color: #265DEB;
  }
  input{
    display: none;
    &:checked + span{
      background-color: #265DEB;
      border-color: #265DEB;
    }
  }
  span{
    width: 16px;
    height: 16px;
    border-radius: 2px;
    border: 1px solid #E5E9F3;
    margin-right: 8px;
    background-color: #fff;
    position: relative;
    &:after{
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: url(../img/checkbox.svg) no-repeat center / contain;
      width: 9px;
      height: 8px;
    }
  }
}



