.radio{
  cursor: pointer;
  margin-bottom: 12px;
  &__block{
    height: 56px;
    display: flex;
    align-items: center;
    padding-left: 18px;
    border: 1px solid #E5E9F3;
    border-radius: 4px;
    transition: .4s;
  }
  input{
    display: none;
    &:checked + .radio__block{
      border-color: #265DEB;
      span{
        background-color: #265DEB;
      }
    }
  }
  b{
    font-weight: bold;
  }
  span{
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid #E5E9F3;
    transition: .4s;
    margin-right: 8px;
  }
}




.n-form__basket {
  width: 624px;
  background-color: #F5F6FA;
  border: 1px solid #E5E9F3;
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
}

.n-basket{
  padding-top: 36px;
  padding-left: 72px;
  padding-right: 72px;
  height: calc(100vh - 300px);
  overflow-y: auto;
  .n-header__basket{
    background-color: #E5E9F3;
    margin-left: 10px;
  }
  .n-header__basket-number{
    color: #0F2D79;
    border-color: #0F2D79;
  }
  .n-header__basket-ico svg{
    fill: #0F2D79;
  }
}
.n-basket__cart{
  position: relative;
  border: 1px solid #E5E9F3;
  border-radius: 4px;
  background-color: #fff;
  padding: 10px 24px 10px 34px;
  min-height: 116px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.n-basket__close{
  position: absolute;
  left: 9px;
  top: 50%;
  height: 100px;
  width: 24px;
  transform: translateY(-50%);
  background: url(../img/basket-close.svg) no-repeat center / contain;
  cursor: pointer;
}

.n-basket__pic{
  width: 120px;
}

.n-basket__info{
  width: calc(100% - 146px);
}

.n-basket__name{
  margin-bottom: 12px;
}
.n-basket__price{
  display: flex;
  justify-content: space-between;
}
.n-basket__summ{
  font-weight: 600;
  font-size: 18px;
  line-height: 110%;
}

.n-basket__inputs {
  max-width: 140px;
  .count__minus, .count__plus{
    height: 28px;
    width: 35px;
  }
  .count__input{
    width: calc(100% - 70px);
  }
  .count__input input{
    height: 28px;
    font-size: 14px;
    font-weight: bold;
  }
}


.n-basket__bottom{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding-top: 75px;
  background: #F5F6FA url(../img/basket-line.png) no-repeat left 0 top 27px / 100% auto;
  padding-bottom: 54px;
  padding-left: 20px;
  padding-right: 20px;
  z-index: 50;
}

.n-basket__panel{
  max-width: 400px;
  margin: 0 auto;
}

.n-basket__prices{
  margin-bottom: 20px;
}

.n-basket__bottom-price{
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  line-height: 100%;
  position: relative;
  margin-bottom: 16px;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 3px;
    width: 100%;
    border-bottom: 1px dotted #0F2D79;
    height: 1px;
  }
  span{
    background-color: #F5F6FA;
    display: inline-block;
    z-index: 10;
    position: relative;
    &:first-child{
      padding-right: 12px;
    }
    &:last-child{
      padding-left: 12px;
    }
  }
}

.n-basket__price-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.n-basket__price-summ{
  span{
    font-size: 14px;
    line-height: 19px;
  }
  p{
    font-weight: 600;
    font-size: 36px;
    line-height: 49px;
  }
}

.n-basket__submit{
  max-width: 210px;
}

.back{
  display: none;
}

.diller{
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: #fff;
  &.active{
    display: flex;
  }
}

.diller__form{
  width: 420px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F5F6FA url(../img/diller.png) no-repeat left top/ 100% auto;
  padding-left: 20px;
  padding-right: 20px;
  .btn{
    margin-top: 12px;
  }
}

.recover__form{
  display: block;
  text-align: center;
  font-size: 14px;
  line-height: 110%;
  width: 100%;
  opacity: .5;
  padding-top: 16px;
}

.diller__text{
  padding-top: 48px;
  max-width: 780px;
  width: 100%;
  padding-left: 90px;
  padding-right: 20px;
  overflow-y: auto;
  height: 100vh;
}

.diller__form-wrap{
  width: 100%;
  max-width: 308px;
  input{
    background-color: #fff;
  }
}

.diller__text{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  p{
    font-size: 16px;
  }
  b{
    font-weight: 700;
  }
}

.diller__text-top{
  width: 100%;
}

.diller__text-bottom{
  align-self: flex-end;
  width: 100%;
  margin-top: 36px;
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 60px;
  border-top: 1px solid #E5E9F3;
}

.diller__form-left{
  width: 50%;
  h4{
    margin-bottom: 22px;
  }
  .btn{
    max-width: 180px;
    font-size: 16px;
  }
}

.diller__form-right{
  max-width: 200px;
  p{
    font-size: 14px;
    & + p{
      margin-top: 12px;
    }
  }
}

.basket-fixed{
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  max-width: 624px;
  z-index: 1500;
  background-color: #F5F6FA;
  box-shadow: 0px 24px 96px 48px #E5E9F3;
  border-radius: 8px 0 0 8px;
  transition: .4s;
  transform: translateX(1000px);
  &.active{
    transform: translateX(0);
  }
  .n-basket{
    height: calc(100vh - 190px);
  }
  .n-basket__bottom{
    background: none;
    background-color: #fff;
    padding-top: 24px;
    padding-bottom: 24px;
  }
  .text {
    padding-top: 16px;
    p{
      margin: 0;
      font-size: 12px;
      font-weight: 600;
      b{
        font-weight: 700;
      }
    }
  }
}


.succes {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2000;
  background-color: rgba(#8D9DC5, .3);
  display: none;
  &.active{
    display: block;
  }
  &__panel{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    max-width: 760px;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 67px 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 24px 96px 48px #E5E9F3;
  }

  h3{
    font-weight: 600;
    font-size: 36px;
    line-height: 110%;
    margin-bottom: 24px;
  }

  p{
    font-size: 14px;
    line-height: 120%;
    b{
      font-weight: bold;
    }
  }
}

.noUi-target{
  height: 1px;
}


.popop-filter {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  padding: 18px 20px 40px;
  border-radius: 12px 12px 0 0;
  background-color: #fff;
  z-index: 1500;
  transform: translateY(100%);
  overflow-y: auto;
  transition: .4s;
  &.popup-filter-nav{
    bottom: auto;
    top: 0;
    height: 100%;
  }
  &.active{
    transform: translateY(0);
  }

  h3{
    padding-bottom: 9px;
    border-bottom: 1px solid #E5E9F3;
    margin-bottom: 24px;
  }
  
  .n-select{
    margin-top: 16px;
  }
  .n-select__main{
    position: relative;
  }
}

#range-before-mobile {
  text-align: right;
  padding-right: 20px;
}

.filter-button-even{
  display: flex;
  margin-bottom: 30px;
  margin-top: 16px;
  border-radius: 4px;
  border: 1px solid #E5E9F3;
  background-color: #F5F6FA;
}

.filter-button-even__catalog{
  width: 50%;
  border-right: 1px solid #E5E9F3;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  
}

.filter-button-even__filter{
  width: 50%;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  span{
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 4px;
    font-weight: bold;
    font-size: 14px;
    line-height: 110%;
    color: #8D9DC5;
    margin-right: 8px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}


@media(max-width: 1600px){
  .catalog-filter__list .n-popular__item{
    width: calc(33.333% - 24px);
  }
}
@media(max-width: 1500px){
  .n-popular__item{
    width: calc(25% - 24px);
  }
}
@media(max-width: 1300px){
  .n-catalog-cart__block{
    flex-wrap: wrap;
  }
  .n-catalog-cart__price{
    width: 100%;
    margin-bottom: 10px;
  }
  .btn.n-catalog-cart__buy{
    width: calc(50% - 5px);
  }
  .btn.n-catalog-cart__question{
    width: calc(50% - 10px);
  }
}
@media(max-width: 1200px){
  .n-form__basket{
    display: none;
  }
  .n-form__block{
    width: 100%;
    padding-right: 0px;
