    display: none;
  }
  .n-catalog-cart__wrap{
    display: block;
  }
  .n-catalog-cart__item{
    width: 100%;
    margin-bottom: 40px;
  }
  .catalog-filter__list .n-popular__item{
    width: calc(50% - 24px);
  }
  
  .n-catalog__item{
    width: calc(25% - 24px);
  }
  .n-popular__item{
    width: calc(33.33% - 24px);
  }
  .n-popular__btns .--pay{
    span{
