@media(max-width: 798px){
  .n-catalog__item{
    width: calc(50% - 24px);
  }
  .n-popular__item{
    width: calc(50% - 24px);
  }
  .n-cart-color{
    flex-wrap: wrap;
    .count,
    .n-cart-color__right{
      width: 100%;
      max-width: 100%;
    }
  }
  .n-cart-color__right{
    margin-top: 12px;
  }
}

@media(max-width: 798px){
  .n-catalog{
    padding-left: 25px;
  }
  .n-catalog__slider {
    padding-right: 50px;
  }
  .n-catalog__slider .swiper-slide{
    width: 315px;
  }
  .n-catalog__item{
    width: 100%;
  }
  .n-catalog.--desktop{
    display: none;
  }
  .n-catalog.--mobile {
    display: block;
  }
  .n-catalog__scroll{
    padding-left: 20px;
    padding-right: 20px;
  }
  .n-catalog-cart__price{
    text-align: center;
    p{
      font-size: 24px;
    }
  }
  .n-catalog-info__price{
    flex-wrap: wrap;
  }

  .n-catalog-info__price-count{
    order: 1;
    width: 100%;
    margin-top: 12px;
    .count{
      max-width: 100%;
    }
  }
}

@media(max-width: 600px){
  .catalog-filter__list .n-popular__item{
    width: 100%;
    margin-right: 0px;
  }
  .succes__panel{
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    justify-content: center;
    transform: none;
    align-items: center;
  }

  .succes__info{
    width: 100%;
    text-align: center;
  }
  .n-popular__item{
    width: 100%;
  }

  .n-basket{
    padding-left: 24px;
    padding-right: 24px;
  }

  .n-basket__price{
