.svgsprite {
    display:inline-block;
    fill:currentColor;
    vertical-align:middle;

    &._accardeon {
    	width:17px;
    	height:11px;
    }
    &._added {
    	width:13px;
    	height:12px;
    }
    &._arrow-crumb {
    	width:10px;
    	height:6px;
    }
    &._arrow-left {
    	width:24px;
    	height:15px;
    }
    &._arrow-right {
    	width:24px;
    	height:15px;
    }
    &._basket {
    	width:16px;
    	height:14px;
    }
    &._box {
    	width:12px;
    	height:14px;
    }
    &._burger {
    	width:26px;
    	height:16px;
    }
    &._calc {
    	width:22px;
    	height:24px;
    }
    &._close {
    	width:14px;
    	height:14px;
    }
    &._count-close {
    	width:13px;
    	height:13px;
    }
    &._drop {
    	width:10px;
    	height:7px;
    }
    &._minus {
    	width:13px;
    	height:2px;
    }
    &._plus {
    	width:13px;
    	height:13px;
    }
    &._question {
    	width:18px;
    	height:18px;
    }
    &._reload {
    	width:25px;
    	height:25px;
    }
    &._search {
    	width:16px;
    	height:17px;
    }
    &._search-white {
    	width:21px;
    	height:22px;
    }
    &._up {
    	width:15px;
    	height:15px;
    }
}