.btn{
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  border: 2px solid transparent;
  font-size: 18px;
  height: 56px;
  &.--blue{
    background-color: #265DEB;
    color: #fff;
  }
  &.--border{
    border-color: #265DEB;
    color: #265DEB;
  }
  &.--orange{
    background-color: $orange;
    border-radius: 4px;
    color: #fff;
    border-color: $orange;
  }
  &.--gray{
    background-color: #F5F6FA;
    border: 1px solid #E5E9F3;
    color: #0F2D79;
    font-weight: bold;
    transition: .4s;
    svg{
      transition: .4s;
      margin-left: 8px;
    }
  }
}